export default {
	data() {
		return {
			couponList: [],
			type: 0,
			isShow: false
		}
	},
	onLoad(params) {
		let _this = this;
		let userInfo = uni.getStorageSync("userInfo");
		_this.type = params.type;
		_this.isShow = _this.type !== "1";
		_this.request({
			url: _this.http.getCouponList,
			data: {
				pageNum: '1',
				pageSize: '100',
				userId: userInfo.userId
			},
			success: (res) => {
				if (res.code === '0') {
					_this.couponList = res.result;
				}
			}
		});
	},
	methods: {
		toOrder(item) {
			if (this.isShow) {
				if (item !== undefined) {
					uni.setStorageSync('coupon', item);
				}
				uni.navigateBack();
			}
		}
	}
}
